.basket {
  padding-bottom: 80px;

  &__container {

    @include onDesktopSmall {
      display: flex;
      max-width: 1136px;
      margin: 0 auto;
      justify-content: space-between;
    }
  }

  &__items-container {
    width: 700px;
  }

  &__item {
    border: 1px solid $c-gray-light;
    width: 650px;

    max-width: 852px;
    height: 128px;
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: space-between;
    margin-bottom: 16px;
    padding: 24px;

    &-button {
      width: 32px;
      height: 32px;
      border: 1px solid $c-secondary;
      cursor: pointer;
      background-color: $c-white;

      &--cres {
        border: none;
        background-color: white;
      }
    }

    &-img {
      width: 66px;
      height: 66px;
      object-fit: contain;
      padding-bottom: 15px;

      @include onTablet {
        padding-right: 31px;
      }
    }

    &--first-row {
      display: flex;
      align-items: center;
    }

    &--second-row {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &__button-container {
        display: flex;
        width: 80px;
        justify-content: space-between;
        align-items: center;
        padding-right: 5px;
      }
    }

    &-price {
      color: $c-primary;
      font-size: 22px;
      font-weight: 700;
      line-height: 140%;
    }
  }

  &__checkout {
    max-width: 752px;
    width: 650px;
    height: 190px;
    border: 1px solid $c-gray-light;
    padding: 24px;

    @include onDesktopSmall {
      width: 368px;
    }

    &-text {
      color: $c-gray-middle;
      text-align: center;
      font-size: 14px;
      font-weight: 500;
      line-height: 21px;
      margin: 0;
      padding-bottom: 58px;
    }

    &-price {
      color: $c-primary;
      font-size: 32px;
      font-weight: 700;
      letter-spacing: -0.32px;
      display: block;
      text-align: center;
      margin: 0 auto;
    }

    &-button {
      background-color: $c-primary;
      width: 320px;
      height: 48px;
      display: block;
      margin: 0 auto;
      box-sizing: border-box;
      border: none;
      position: relative;
      cursor: pointer;

      &::before {
        content: "";
        width: 320px;
        height: 1px;
        background: $c-gray-light;
        position: absolute;
        transform: translate(-129px, -50px);
      }

      &--text {
        text-decoration: none;
        color: $c-white;
        font-size: 14px;
        font-weight: 600;
        line-height: 21px;
      }
    }
  }
}
